/******* navigation ***********/ 

.nav {
    width:100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding:0 40px;
    align-items: center;
    height:70px;
    font-size: 18px;
    position: sticky;
    border-bottom:1px solid white;
    @include tablette {
        transition: 0.8s all ease-in-out;
        position: fixed;
        top: 0;
        left: -100%;
        background: #000000;
        width: 390px !important;
        height: 100vh;
        opacity: 0.9;
        z-index: 999;
    }
    .nav__logo {
        @include tablette {
            display: flex;
        }
        img {
            @include iphone12 {
                width:200%;
            }
        }
        
    }
    .nav__menu {
        a {
            padding: 0 10px;
            color: $gray;
            font-family: $poppins;
            @include tablette {
                display: flex;
                flex-direction: column;
                justify-content: center;
            }
        }
    }
    &.active{
        left: 0;
    }

}

/********* menu burger *****************/

// BURGER MENU
.burger{
    display: none;
    @include tablette {
        display: block;
    }
    position: fixed;
    top: 4.4%;
    right: 2%;
    width: 50px;
    height: 50px;
    z-index: 2;
    cursor: pointer;
}
.burger span{
    width: 40px;
    position: absolute;
    top: 23%;
    left: 7%;
    border-bottom: 3px solid white;
    transition: 0.3s all ease-in-out;
}
.burger.active span:nth-of-type(1){
    top: 47%;
    left: 7%;
    transform: rotate(45deg);
    transition: 0.3s all ease-in-out;
}
.burger span:nth-of-type(2){
    opacity: 1;
    width: 40px;
    position: absolute;
    top: 47%;
    left: 7%;
    border-bottom: 3px solid white;
}
.burger.active span:nth-of-type(2){
    transition: 0.3s all ease-in-out;
    opacity: 0;
}
.burger span:nth-of-type(3){
    width: 40px;
    position: absolute;
    top: 70%;
    left: 7%;
    border-bottom: 3px solid white;
}
.burger.active span:nth-of-type(3){
    top: 47%;
    left: 7%;
    transform: rotate(-45deg);
    transition: 0.3s all ease-in-out;
}